.title{
	text-align: center;
}
.task{
	margin-left: 3px;
	margin-top: 3px;
	padding: 4px; 
}
.todolist .task:hover{
	background:lightgray;
}
.todolist .task .delete{
	color:blue;
	float: right;
	margin-right: 15px;
	cursor: pointer;
	display: none;
	font-weight: bold;
}
.todolist .task:hover .delete{
	display: inline-block;
}
.input-todo{
	border-color: red;
	width:250px;
	margin: 5px 15px 15px 3px;
}
.input-checked-task{
	margin-right: 5px;
}

.todolist .task.done {
	text-decoration: line-through;
}