@import url(https://fonts.googleapis.com/css?family=Marmelad);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html,
body,
#root {
  height: 100%;
}

.App {
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: center;
	        justify-content: center;
	-webkit-align-items: center;
	        align-items: center;
	-webkit-flex-direction: column;
	        flex-direction: column;
}
body {
  background: -webkit-linear-gradient(bottom left, #71AE51 1%, #F3BA4B 25%, #E6873B 40%, #CF4845 60%, #8B4593 80%, #449CD7 100%);
  background: linear-gradient(to top right, #71AE51 1%, #F3BA4B 25%, #E6873B 40%, #CF4845 60%, #8B4593 80%, #449CD7 100%);
}



.clock{
	color: white;
	font-size:3em;
}

.main{
	margin: 100px;
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: center;
	        justify-content: center;
	-webkit-align-items: center;
	        align-items: center;
	-webkit-flex-direction: column;
	        flex-direction: column;
}

.main-information{
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: center;
	        justify-content: center;
	-webkit-align-items: center;
	        align-items: center;
	-webkit-flex-direction: column;
	        flex-direction: column;
	margin-top: 50px;
	
}
button{
	margin-top: 30px;
	width: 150px;
}
.form-control{
	border:none;
}
.title{
	text-align: center;
}
.task{
	margin-left: 3px;
	margin-top: 3px;
	padding: 4px; 
}
.todolist .task:hover{
	background:lightgray;
}
.todolist .task .delete{
	color:blue;
	float: right;
	margin-right: 15px;
	cursor: pointer;
	display: none;
	font-weight: bold;
}
.todolist .task:hover .delete{
	display: inline-block;
}
.input-todo{
	border-color: red;
	width:250px;
	margin: 5px 15px 15px 3px;
}
.input-checked-task{
	margin-right: 5px;
}

.todolist .task.done {
	text-decoration: line-through;
}
.wrapper{
	margin: 100px;
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: center;
	        justify-content: center;
	-webkit-align-items: center;
	        align-items: center;
	-webkit-flex-direction: column;
	        flex-direction: column;
}

.hello-title{
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: center;
	        justify-content: center;
	-webkit-align-items: center;
	        align-items: center;
	-webkit-flex-direction: row;
	        flex-direction: row;
	margin-top: 50px;
	color: white;
	font-family: 'Marmelad', sans-serif;
}

